import React from 'react'
import {Navbar,Nav,Image} from 'react-bootstrap'

class Header extends React.Component{
    render(){
        return(
            <Navbar bg="white" sticky="top" expand="lg">
                <Navbar.Brand href="/-">
                    <Image className="company-logo" src='./GFIlogo.png'/>
                    <span>GREENFIRE INDIA</span>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="ml-auto">
                    <Nav.Link href="/-/registration">Student Registeration</Nav.Link>
                    <Nav.Link href="/-/trainer-registration">Trainer Registeration</Nav.Link>
                    <Nav.Link href="/-/payment">Payment</Nav.Link>
                    <Nav.Link href="/-/admin">Login</Nav.Link>
                </Nav>
                </Navbar.Collapse>
            </Navbar>
        )
    }
}

export default Header