import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import './App.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { Route, BrowserRouter as Router, Switch } from 'react-router-dom'
import studentRegistration from './studentRegistration.js'
import trainerRegistration from './trainerRegistration.js'
import payment from './payment.js'
import Header from './header.js'
import Footer from './footer.js'
import Admin from './admin.js'
import 'bootstrap/dist/css/bootstrap.min.css';

ReactDOM.render(
    <Router>
      <div className="master">
        <Header/>
        <Switch>
          <Route exact path="/-" component={App} />
          <Route path="/-/registration" component={studentRegistration} />
          <Route path="/-/trainer-registration" component={trainerRegistration} />
          <Route path="/-/payment" component={payment} />
          <Route path="/-/demo-registration" component={studentRegistration} />
          <Route path="/-/admin" component = {Admin}/>
          {/* <Route component={App} /> */}
        </Switch>
        {window.location.pathname!=='/-/admin'&&<Footer/>}
      </div>
    </Router>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
