import React from 'react';
import {Button,Jumbotron,Form,Tab,Tabs,Table,Nav} from 'react-bootstrap'
import creds from './client_secret.json';
import {JWT} from 'google-auth-library'

const SHEET_ID = '1Rz_WmYixQazKYLGde_CC_6v42zaYzbttxDtuTNYZ5gM';
var ACCESS_TOKEN = ''

class Admin extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            isAdmin:0,
            studentData:[],
            trainerData:[]
        }
    this.username=''
    this.password=''
    }
    componentDidMount() {
        this.authenticator()
    }
    async authenticator(){
        const GOOGLE_AUTH_SCOPE = ["https://spreadsheets.google.com/feeds"];
        var jwt_client = await new JWT(
          { email:creds.client_email, 
            key :creds.private_key, 
            scopes:GOOGLE_AUTH_SCOPE, 
            subject: null});
        await jwt_client.authorize(function (err, token) {
          ACCESS_TOKEN = token.access_token
        })
    }

    onChangeFn=(event)=>{
        if(event.target.id==='username'){
            this.username=event.target.value
        }
        else{
            this.password=event.target.value
        }
    }

    onLoginSubmit=()=>{
        if(this.username==='admin'&&this.password==='Admin@1234'){
            this.setState({isAdmin:1})
        }
        else{
            this.setState({isAdmin:2})
        }
    }

    loginUI(){
        return(
            <Jumbotron className="form-jumbo">
                <h2>Greenfire India Login</h2>
                <Form>
                    <Form.Group controlId="username">
                        <Form.Label>User name</Form.Label>
                        <Form.Control type="text" placeholder="user name" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="password">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="password" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Button variant="primary" onClick={this.onLoginSubmit}>
                        Submit
                    </Button>
                </Form>
            </Jumbotron>
        )
    }

    getDetails=async()=>{
        const studentRequest = await fetch(`https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/Student_Database!A2:G100`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${ACCESS_TOKEN}`,
          }
          })
          const studentSheetValues = await studentRequest.json()
          this.setState({studentData:studentSheetValues.values})
          const trainerRequest = await fetch(`https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/Trainer_Database!A2:G100`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${ACCESS_TOKEN}`,
          }
          })
          const trainerSheetValues = await trainerRequest.json()
          this.setState({trainerData:trainerSheetValues.values})
    }

    tableUI(type){
        const studentHeadings = ['Class interested in','Parent Name','Child Name','Age','Phone Number','Email','Referal Code']
        const trainerHeadings = ['Name','Phone Number','Email','Expert in','Level of Expertise','Years of Expertise']
        var headings=[]
        type === 'student' ? headings = studentHeadings : headings = trainerHeadings
        var data = []
        type === 'student' ? data = this.state.studentData : data = this.state.trainerData

        return(
            <Table responsive striped bordered hover size="sm">
                <thead>
                    <tr>
                        {headings.map(heading=>(
                            <th>{heading}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {data.map(datum=>(
                        <tr>
                            {datum.map(detail=>(
                                <td>{detail}</td>
                            )
                            )}
                        </tr>
                    ))}
                </tbody>
            </Table>
        )
    }

    adminUI(){
        // setTimeout(this.getDetails,2000)
        return(
            <div>
                <div className="d-flex justify-content-between mb-3">
                <h4>Welcome admin</h4>
                <Button variant="success" onClick={this.getDetails}>Load Data</Button>
                {/* <Link className ="mr-5" to="/">Logout</Link> */}
                <Nav>
                    <Nav.Link href="/-/admin">Logout</Nav.Link></Nav></div>
                <Tabs defaultActiveKey="student_details" id="uncontrolled-tab-example">
                    <Tab eventKey="student_details" title="Student details">
                        {this.tableUI('student')}
                    </Tab>
                    <Tab eventKey="trainer_details" title="Trainer details">
                        {this.tableUI('trainer')}
                    </Tab>
                </Tabs>
            </div>
        )
    }

    wrongCredentialsUI(){
        return(
            <div>
                <h5>Wrong Credentials</h5>
                <Nav>
                    <Nav.Link href="/-/admin">Login again</Nav.Link>
                    <Nav.Link href="/-">Home</Nav.Link>
                </Nav>
            </div>
        )
    }

    render(){
        return(
            <div>
            {this.state.isAdmin===0?
                this.loginUI()
            :this.state.isAdmin===1?
                this.adminUI()
                :
                this.wrongCredentialsUI()}
            </div>
        )
    }
}

export default Admin