import React from 'react'
import {Form,Button,Jumbotron,Image} from 'react-bootstrap'
import creds from './client_secret.json';
import {JWT} from 'google-auth-library'
import submitted from './assets/submitted.png'


const SHEET_ID = '1Rz_WmYixQazKYLGde_CC_6v42zaYzbttxDtuTNYZ5gM';
var ACCESS_TOKEN = ''
var postData = []
class trainerRegistration extends React.Component {
    constructor(props){
        super(props);
        this.state={
            buttonDisable : true,
            isSubmitted : false
        }
    }
    componentDidMount() {
        this.authenticator()
    }
    async authenticator(){
        const GOOGLE_AUTH_SCOPE = ["https://spreadsheets.google.com/feeds"];
        var jwt_client = await new JWT(
          { email:creds.client_email, 
            key :creds.private_key, 
            scopes:GOOGLE_AUTH_SCOPE, 
            subject: null});
        await jwt_client.authorize(function (err, token) {
          ACCESS_TOKEN = token.access_token
        })
    }
    onChangeFn=(event)=>{
        postData[parseInt(event.target.id)]=event.target.value
    }
    onTrainerSubmit=()=>{
        fetch(`https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/Trainer_Database!A2:G2:append?valueInputOption=USER_ENTERED`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${ACCESS_TOKEN}`,
          },
          body: JSON.stringify({
            "range": "Trainer_Database!A2:G2",
            "majorDimension": "ROWS",
            "values": [
              postData
            ],
          })
          })
          this.setState({isSubmitted:true})
    }
    trainerRegistrationUI(){
        return (
            <Jumbotron className="form-jumbo">
                <h2>Trainer registration</h2>
                <Form>
                    <Form.Group controlId="0">
                        <Form.Label>Name</Form.Label>
                        <Form.Control type="text" placeholder="Name followed by initial" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="1">
                        <Form.Label>Phone Number</Form.Label>
                        <Form.Control type="text" placeholder="In numbers" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="2">
                        <Form.Label>Email</Form.Label>
                        <Form.Control type="email" placeholder="email" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="3">
                        <Form.Label>Expert in</Form.Label>
                        <Form.Control as="select" onChange={this.onChangeFn}>
                            <option>Select</option>
                            <option>Drawing</option>
                            <option>Dance</option>
                            <option>Chess</option>
                            <option>Calligraphy</option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="4">
                        <Form.Label>Level of Expertise</Form.Label>
                        <Form.Control as="select" onChange={this.onChangeFn}>
                            <option>Select</option>
                            <option>Beginner</option>
                            <option>Medium</option>
                            <option>Advanced</option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="5">
                        <Form.Label>Years of Expertise</Form.Label>
                        <Form.Control type="text" placeholder="" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="6">
                        <Form.Check onClick={()=>{
                            this.setState({buttonDisable:!this.state.buttonDisable})
                        }} type="checkbox" label="Accept Terms and Conditions" />
                        <Form.Text className="text-muted">Need terms and Conditions</Form.Text>
                    </Form.Group>

                    <Button disabled={this.state.buttonDisable} variant="primary" onClick={this.onTrainerSubmit}>
                        Submit
                    </Button>
                </Form>
            </Jumbotron>
        )
    }
    render(){
        return(
            <>
                {!this.state.isSubmitted?this.trainerRegistrationUI():<Image src={submitted} alt="submitted"/>}
            </>
        )
    }
}
export default trainerRegistration