import {Image,Carousel,Jumbotron, Container,Row,Col} from 'react-bootstrap'
import React from 'react'
import chess from './assets/chess-r.jpg'
import chessMobile from './assets/chess-mobile.jpg'
import drawing from './assets/drawing-1-r.png'
import drawingMobile from './assets/drawing-mobile.png'
import dance from './assets/dance.jpg'
import danceMobile from './assets/dance-mobile.jpg'
import calligraphy from './assets/calligraphy-1-r.png'
