import React from 'react'
import { AiOutlineMail,AiOutlineMobile,AiOutlineWhatsApp,AiOutlineFacebook,AiOutlineTwitter,AiOutlineHeart } from "react-icons/ai";
import {Container,Row,Col} from 'react-bootstrap'

class Footer extends React.Component{
    render(){
        var isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
        var nameOfClass = ''
        isMobile ? nameOfClass = "mr-2 social-media" : nameOfClass = "mr-4 social-media"
        return(
            <Container className="footer w-100">
                <Row className="contact-us-heading text-center ">
                    <Col><h3 className="mt-5">CONTACT US</h3></Col>
                </Row>
                <Row className="text-center mb-3">
                    <Col>---------- Need to be updated ----------</Col>
                </Row>
                <Row className="text-center mb-3">
                    <Col><a className="footer-link" href="mailto:contactme.greenfire@gmail.com"><AiOutlineMail className="mr-2 social-media"/>contactme.greenfire@gmail.com</a></Col>
                    <Col><a className="footer-link" href="tel:7385891067"><AiOutlineMobile className="mr-2 social-media"/>+91 7385891067</a></Col>
                </Row>
                <Row className="text-center mb-3">
                <Col><a className="footer-link" href="https://wa.me/917385891067"><AiOutlineWhatsApp className={nameOfClass}/></a><a className="footer-link" href="https://www.facebook.com/greenfireindia/?ref=page_internal"><AiOutlineFacebook className={nameOfClass}/></a><AiOutlineTwitter className={nameOfClass}/></Col>
                <Col>Copyright  2020 {isMobile?"GFI":"Greenfire India"}</Col>
                </Row>
                <Row className="text-center">
                    <Col>Made with <AiOutlineHeart className="heartin"/> in India</Col>
                </Row>
            </Container>
        )
    }
}

export default Footer