import React from 'react'
import {Tabs,Tab,Form,Button,Jumbotron,Image} from 'react-bootstrap'
import creds from './client_secret.json';
import {JWT} from 'google-auth-library'
import submitted from './assets/submitted.png'

const SHEET_ID = '1Rz_WmYixQazKYLGde_CC_6v42zaYzbttxDtuTNYZ5gM';
var ACCESS_TOKEN = ''
var postData = []
class studentRegistration extends React.Component {
    constructor(props){
        super(props);
        this.state={
            isSubmitted : false
        }
    }
    componentDidMount() {
        this.authenticator()
    }
    async authenticator(){
        const GOOGLE_AUTH_SCOPE = ["https://spreadsheets.google.com/feeds"];
        var jwt_client = await new JWT(
          { email:creds.client_email, 
            key :creds.private_key, 
            scopes:GOOGLE_AUTH_SCOPE, 
            subject: null});
        await jwt_client.authorize(function (err, token) {
          ACCESS_TOKEN = token.access_token
        })
    }

    demoRegistration(){
        return(
            <Jumbotron className="form-jumbo">
                <h2>Demo Registration</h2>
                ------Need form details------
            </Jumbotron>
        )
    }

    onChangeFn=(event)=>{
        postData[parseInt(event.target.id)]=event.target.value
    }

    onClassSubmit=()=>{
        fetch(`https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/Student_Database!A2:G2:append?valueInputOption=USER_ENTERED`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${ACCESS_TOKEN}`,
          },
          body: JSON.stringify({
            "range": "Student_Database!A2:G2",
            "majorDimension": "ROWS",
            "values": [
              postData
            ],
          })
          })
          this.setState({isSubmitted:true})
    }

    classRegistration(){
        return(
            <Jumbotron className="form-jumbo">
                <h2>Class registration</h2>
                <Form>
                <Form.Group controlId="0">
                    <Form.Label>Class interested in</Form.Label>
                    <Form.Control as="select" onChange={this.onChangeFn}>
                            <option>Select</option>
                            <option>Drawing</option>
                            <option>Dance</option>
                            <option>Chess</option>
                            <option>Calligraphy</option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="1">
                        <Form.Label>Parent Name</Form.Label>
                        <Form.Control type="text" placeholder="Name followed by initial" onChange={this.onChangeFn} />
                    </Form.Group>

                    <Form.Group controlId="2">
                        <Form.Label>Child Name</Form.Label>
                        <Form.Control type="text" placeholder="Name followed by initial" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="3">
                        <Form.Label>Age</Form.Label>
                        <Form.Control type="text" placeholder="In numbers" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="4">
                        <Form.Label>Phone Number</Form.Label>
                        <Form.Control type="text" placeholder="In numbers" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="5">
                        <Form.Label>Email</Form.Label>
                        <Form.Control type="email" placeholder="email" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Form.Group controlId="6">
                        <Form.Label>Referral Code</Form.Label>
                        <Form.Control type="text" placeholder="" onChange={this.onChangeFn}/>
                    </Form.Group>

                    <Button variant="primary" onClick={this.onClassSubmit}>
                        Submit
                    </Button>
                </Form>
            </Jumbotron>
        )
    }

    render(){
        console.log('ul',window.location.pathname)
        return (
        <div className="App">
            <h1>Student Registration</h1>
            <Tabs defaultActiveKey={window.location.pathname} id="uncontrolled-tab-example">
                <Tab eventKey="/-/registration" title="Class Registration">
                    {!this.state.isSubmitted?this.classRegistration():<Image src={submitted} alt="submitted"/>}
                </Tab>
                <Tab eventKey="/-/demo-registration" title="Demo Registration">
                    {this.demoRegistration()}
                </Tab>
            </Tabs>
        </div>
        )
    }
}
export default studentRegistration